import React, { useState, useEffect } from 'react';

import { Row, Col, Modal, Button, Rate } from 'antd';
import { ArrowLeftOutlined, ArrowRightOutlined, DeleteOutlined, EditOutlined, FileSyncOutlined, SwapOutlined } from '@ant-design/icons';

import TextDialog from '../Modals/TextDialog';
import DirNodeDialog from '../Modals/DirNodeDialog';

function ReaderModalContextMenu(props) {
  const [jumpValue, setJumpValue] = useState(0);
  useEffect(() => {
    setJumpValue(props.initialValue + 1);
  }, [props.initialValue]);

  const changeJumpValue = (val) => {
    setJumpValue(Math.min(Math.max(val, 1), props.albumCm.pageCount));
  };

  const [showRenameModal, setShowRenameModal] = useState(false);
  const [showDirNodeModal, setShowDirNodeModal] = useState(false);
  //const [showConfirmDelete, setShowConfirmDelete] = useState(false);

  const [tierDisplay, setTierDisplay] = useState(0);
  useEffect(() => {
    if (props.albumCm === undefined) { return; }
    setTierDisplay(props.albumCm.tier);
  }, [props.albumCm]);

  if (props.albumCm === undefined) { return (<></>); }

  return (
    <>
      <Modal
        visible={props.visible}
        onCancel={props.onCancel}
        footer={null}
        closable={false}
        centered={true}
        style={{ maxWidth: 300 }}
      //bodyStyle={{ border: "1px solid black" }}
      >
        <Row>
          {props.type !== 1 ? 
            <Col span={24} style={{ marginBottom: 15, textAlign: "center" }}>
              <Rate
                count={3}
                value={tierDisplay}
                onChange={(value) => { setTierDisplay(value); props.onTierChange(value); }}
                style={{ fontSize: "40px" }}
              />
            </Col>
          : "" }
          <Col span={24}>
            <Button type="primary" onClick={() => props.onRecount()} style={{ width: "100%" }}>
              <FileSyncOutlined />Recount Pages
            </Button>
          </Col>
          <Col span={12} style={{ textAlign: "left", marginTop: 5 }}>
            <input type="number" value={jumpValue} onChange={(e) => changeJumpValue(e.target.value)} style={{ width: "97%", padding: "4px", WebkitBoxSizing: "border-box", borderRadius: "5px", color: "black" }} />
          </Col>
          <Col span={6} style={{ textAlign: "left", marginTop: 5 }}>
            <Button type="ghost" style={{ width: "97%", color: "white" }}
              onClick={() => changeJumpValue(jumpValue - 1)}
            >-</Button>
          </Col>
          <Col span={6} style={{ textAlign: "right", marginTop: 5 }}>
            <Button type="ghost" style={{ width: "97%", color: "white" }}
              onClick={() => changeJumpValue(jumpValue + 1)}
            >+</Button>
          </Col>
          <Col span={24} style={{ marginTop: 5 }}>
            <Button type="primary" onClick={() => props.onJump(jumpValue - 1)} style={{ width: "100%" }}>
              <ArrowRightOutlined />Jump
            </Button>
          </Col>
          <Col span={24} style={{ marginTop: 5 }}>
            <Button type="primary" onClick={() => props.onUndoJump()} style={{ width: "100%" }}>
              <ArrowLeftOutlined />Undo Jump
            </Button>
          </Col>
          <Col span={24} style={{ marginTop: 5 }}>
            <Button type="primary" onClick={() => { props.onCancel(); setShowRenameModal(true); }} style={{ width: "100%" }}>
              <EditOutlined />Rename Page
            </Button>
          </Col>
          {props.type === 1 ? 
            <Col span={24} style={{ marginTop: 5 }}>
              <Button type="primary" onClick={() => { props.onCancel(); setShowDirNodeModal(true); }} style={{ width: "100%" }}>
                <SwapOutlined />Move Page
              </Button>
            </Col>
          : ""}
          <Col span={24} style={{ marginTop: 5 }}>
            <Button type="danger" onClick={() => { props.onCancel(); props.onDelete(); }} style={{ width: "100%" }}>
              <DeleteOutlined />Delete Page
            </Button>
          </Col>
        </Row>
      </Modal>
      <TextDialog
        visible={showRenameModal} onClose={() => setShowRenameModal(false)}
        initialValue={props.pageName}
        onOk={props.onRename}
      />
      {props.type === 1 ? 
        <DirNodeDialog 
          visible={showDirNodeModal} 
          onClose={() => setShowDirNodeModal(false)}
          onOk={props.onMove}
        />
      :""}
    </>
  )
}

export default ReaderModalContextMenu;