import React from 'react';
import { Button, Input } from 'antd';
import { Row, Col } from 'antd';

function MyInputTextbox(props) {
  function handleChange(event) {
    props.onChange(props.label, event.target.value);
  }

  const colSize = {
    input: props.showClearButton ? 14 : 18,
    button: props.showClearButton ? 4 : 0
  }

  return (
    <>
      <Row gutter={[0, 8]}>
        <Col sm={6}>
          <label className='my-editor-label'>{props.label}</label>
        </Col>
        <Col sm={colSize.input}>
          {
            props.row == null ?
            <Input
              placeholder={props.placeholder}
              value={props.value}
              onChange={handleChange}
            /> :
            <Input.TextArea 
              placeholder={props.placeholder}
              value={props.value}
              onChange={handleChange}
              autoSize={{minRows: props.row, maxRows:props.row}}
            />
          }
        </Col>
        {
          props.showClearButton ?
          <Col sm={colSize.button}>
            <Button 
              style={{ width: "100%", marginLeft:"5px" }}
              onClick={() => props.onChange(props.label, '')}
            >
              <span className='my-editor-button-label'>Clear</span>
            </Button>
          </Col>:
          <></>
        }
      </Row>
    </>
  );
}

export default MyInputTextbox;