import React, { useState, useEffect } from 'react';

import { Row, Col, Dropdown } from 'antd';
import { useSwipeable } from 'react-swipeable';

import EditModalTrigger from '../../Albums/EditModalTrigger';
import ReaderModalContextMenu from './ReaderModalContextMenu';
import ReaderModalChapterDrawer from './ReaderModalChapterDrawer';
import { IS_PUBLIC } from '../../_utils/Config';
import loadingImg from '../../_resources/loading.gif';
import withMyAlert from '../HOCs/withMyAlert';
import withPageManager from '../HOCs/withPageManager';
import * as _helper from '../../_utils/Helper';
import * as _uri from "../../_utils/UriHelper";

const axios = require('axios').default;

function ReaderModal(props) {
  const [apm, setApm] = useState({ //apm = album pagination model
    path: null,
    lpi: 0,
    pages: [],
    indexes: {
      cPageI: 0,
      pPageI: 0,
      triad: "zenpen",
      zenpenIndex: 0,
      chuhenIndex: 0,
      kouhenIndex: 0,
    },
    isLandscape: false,
    includeDetail: false,

    restartPageTrigger: false,
  });

  useEffect(() => {
    if (props.albumCm === undefined || props.albumCm === null) { return; }
    if (props.albumCm.path === apm.path) { return; }
    refreshPages(false);
    
  }, [props.albumCm]);

  const refreshPages = (restartPage) => {
    axios.get(_uri.GetAlbumPageInfos(props.type, props.albumCm.path, apm.includeDetail))
      .then(function (response) {
        setApm({
          path: props.albumCm.path,
          lpi: restartPage ? 0 : props.albumCm.lastPageIndex,
          isLandscape: response.data.orientation === "Landscape",
          pages: response.data.fileInfos,
          indexes: null,
          includeDetail: apm.includeDetail,
          restartPageTrigger: restartPage ? !apm.restartPageTrigger : apm.restartPageTrigger
        });

        if (props.albumCm.pageCount !== response.data.fileInfos.length & props.type !== 1) {
          console.log(`PageCount (${props.albumCm.pageCount}) does not match fileInfos length (${response.data.fileInfos.length})`);
          axios.get(_uri.RefreshAlbum(props.type, props.albumCm.path))
            .then(function (response) {
            })
            .catch(function (error) {
              props.popApiError(error);
            });
        }
      })
      .catch(function (error) {
        props.popApiError(error);
      });
  }

  useEffect(() => {
    const targetPage = apm.lpi < apm.pages.length ? apm.lpi : apm.pages.length - 1;

    pageHandler.jumpTo(targetPage);
  }, [apm.path, apm.restartPageTrigger]);

  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown2, false);

    return () => { //component will unmount
      document.removeEventListener("keydown", handleKeyDown2, false);
    };
  }, []);

  const handleKeyDown2 = (event) => {
    if (event.keyCode === 27) {
      pageHandler.close();
    }
  };

  //#region Event handlers
  const pageHandler = {
    jumpTo: function (page, renameObj) {
      const maxIndex = apm.pages.length - 1;
      const newPPageI = apm.indexes?.cPageI ?? 0;
      const newCPageI = _helper.clamp(page, 0, maxIndex);

      const { newTriad, zenpenIndex, chuhenIndex, kouhenIndex } = (() => {
        function compose(newTriad, zenpenIndex, chuhenIndex, kouhenIndex) {
          return {
            newTriad,
            zenpenIndex,
            chuhenIndex,
            kouhenIndex
          };
        }

        function pageCeil(modifier) {
          const pagesLen = apm.pages.length;
          return (((newCPageI + modifier) % pagesLen) + pagesLen) % pagesLen;
        }

        if (newCPageI % 3 === 0) {
          return compose("zenpen", newCPageI, pageCeil(1), pageCeil(-1));
        }
        if (newCPageI % 3 === 1) {
          return compose("chuhen", pageCeil(-1), newCPageI, pageCeil(1));
        }
        if (newCPageI % 3 === 2) {
          return compose("kouhen", pageCeil(1), pageCeil(-1), newCPageI);
        }
      })();

      let modifiedPages = apm.pages;
      if (renameObj !== null && renameObj !== undefined) {
        modifiedPages[renameObj.index] = renameObj.newPageInfo;
      }

      setApm({
        indexes: {
          cPageI: newCPageI,
          pPageI: newPPageI,
          triad: newTriad,
          zenpenIndex: zenpenIndex,
          chuhenIndex: chuhenIndex,
          kouhenIndex: kouhenIndex
        },
        path: apm.path,
        lpi: apm.lpi,
        isLandscape: apm.isLandscape,
        includeDetail: apm.includeDetail,
        pages: modifiedPages,
      });

      if (apm.path != null && page === apm.pages.length - 1 && props.type !== 1) {
        axios.post(_uri.UpdateAlbumOuterValue(props.type), {
          albumPath: apm.path,
          lastPageIndex: page
        })
          .then((response) => {
          })
          .catch((error) => {
            props.popApiError(error);
          });
      }
    },
    rename: (newVal, oldVal) => {
      const srcAlRelPath = currentPageInfo.uncPathEncoded;
      const dstAlRelPath = (() => {
        var arr = currentPageInfo.uncPathEncoded.split('\\');
        arr.pop();
        arr.push(newVal);

        return _helper.pathJoin(arr, '\\');
      })();

      const movObj = {
        overwrite: false,
        src: {
          albumPath: apm.path,
          alRelPath: srcAlRelPath,
        },
        dst: {
          albumPath: apm.path,
          alRelPath: dstAlRelPath
        }
      };

      props.movePage(movObj, 
        (response) => {
          const oldPageIdx = apm.pages.findIndex(e => e.uncPathEncoded === srcAlRelPath);
          const oldPage = apm.pages.splice(oldPageIdx, 1)[0];
          const renamedPage = {
            ...oldPage,
            name: newVal,
            uncPathEncoded: dstAlRelPath
          };
          
          apm.pages = apm.pages.filter(e => e.uncPathEncoded !== dstAlRelPath);//this line is for overwrite
          apm.pages.push(renamedPage);
          apm.pages.sort((a, b) => a.uncPathEncoded.localeCompare(b.uncPathEncoded));

          setApm((prev) => {
            return ({
              ...prev
            });
          });
        },
        (error) => {
          props.popApiError(error);
        }
      )
    },
    move: (newDir) => {
      console.log('reader model move', newDir);

      const srcAlRelPath = currentPageInfo.uncPathEncoded;

      const movObj = {
        overwrite: false,
        src: {
          albumPath: apm.path,
          alRelPath: srcAlRelPath,
        },
        dst: {
          albumPath: newDir,
          alRelPath: currentPageInfo.name
        }
      };

      props.movePage(movObj, 
        (response) => {
          removePageFromApm(currentPageInfo.uncPathEncoded);
          setApm((prev) => {
            return ({
              ...prev
            });
          });
        },
        (error) => {
          props.popApiError(error);
        }
      )
    },
    delete: (pageInfo) => {
      const delObj = {
        albumPath: apm.path,
        alRelPath: pageInfo.uncPathEncoded
      };

      props.deletePage(delObj,
        (response) => {
          removePageFromApm(pageInfo.uncPathEncoded);
          setApm(prev => {
            return{
              ...prev
            }
          });
        },
        (error) => {
          props.popApiError(error);
        });
    },
    close: () => {
      props.onClose(apm.path, apm.indexes.cPageI);
    },
    chapterDeleteSuccess: (newPageCount) => {
      props.onChapterDeleteSuccess(apm.path, newPageCount);
      axios.get(_uri.GetAlbumPageInfos(props.type, props.albumCm.path))
      .then(function (response) {
        const newIndexes = clampIndexes(apm.indexes, response.data.fileInfos.length - 1);

        setApm({
          path: props.albumCm.path,
          lpi: props.albumCm.lastPageIndex,
          isLandscape: response.data.orientation === "Landscape",
          pages: response.data.fileInfos,
          indexes: {...apm.indexes, ...newIndexes},
          includeDetail: apm.includeDetail
        });
      })
      .catch(function (error) {
        props.popApiError(error);
      });
    },
    chapterRenameSuccess: () => {
      refreshPages(true);
    }
  }

  function removePageFromApm(uncPathEncoded){
    const max = apm.pages.length - 2;
    const newIndexes = clampIndexes(apm.indexes, max);
    apm.indexes = { ...apm.indexes,...newIndexes};
    
    apm.pages = apm.pages.filter(page => page.uncPathEncoded !== uncPathEncoded);
  }

  function clampIndexes(indexes, max){
    return{
      cPageI:_helper.clamp(indexes.cPageI, 0, max),
      pPageI:_helper.clamp(indexes.pPageI, 0, max),
      zenpenIndex:_helper.clamp(indexes.zenpenIndex, 0, max),
      chuhenIndex:_helper.clamp(indexes.chuhenIndex, 0, max),
      kouhenIndex:_helper.clamp(indexes.kouhenIndex, 0, max)
    }
  }

  function changeDetailVisibility(){
    const includeDetail = !apm.includeDetail;

    if(includeDetail && apm.pages[0].createDate === null){
      axios.get(_uri.GetAlbumPageInfos(props.type, props.albumCm.path, includeDetail))
      .then(function (response) {
        setApm(prev => {
          prev.includeDetail = includeDetail;
          prev.pages = response.data.fileInfos
          return { ...prev };
        });
      })
      .catch(function (error) {
        props.popApiError(error);
      });
    }
    else{
      setApm(prev => {
        prev.includeDetail = includeDetail;
        return { ...prev };
      });
    }
  }

  const albumHandler = {
    tierChange: (value) => {
      axios.put(_uri.UpdateAlbumTier(props.type), {
        albumPath: apm.path,
        tier: value
      })
        .then(function (response) {
          console.log(response.data);
        })
        .catch(function (error) {
          props.popApiError(error);
        });
    },
    recount: () => {
      axios.get(_uri.RecountAlbumPages(props.type, apm.path))
        .then(function (response) {
          console.log("new page count", response.data);
          props.onChapterDeleteSuccess(apm.path, response.data);
        })
        .catch(function (error) {
          props.popApiError(error);
        });
    }
  }

  const [showContextMenu, setShowContextMenu] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);

  const buttonHandler = {
    rightTop: () => {
      if (apm.isLandscape) { setShowDrawer(true); }
      else { pageHandler.close(); }
    },
    rightMid: () => {
      if (apm.isLandscape) { changeDetailVisibility(); }
      else { pageHandler.jumpTo(apm.indexes.cPageI + 1); }
    },
    rightBot: () => {
      if (apm.isLandscape) { pageHandler.close(); }
      else { pageHandler.jumpTo(apm.pages.length - 1); }
    },
    midTop: () => {
      if (apm.isLandscape) { pageHandler.jumpTo(apm.indexes.cPageI - 1); }
      else { changeDetailVisibility(); }
    },
    midMid: () => {
      setShowContextMenu(true);
    },
    midBot: () => {
      if (apm.isLandscape) { pageHandler.jumpTo(apm.indexes.cPageI + 1); }
      else { }
    },
    leftTop: () => {
      if (apm.isLandscape) { pageHandler.jumpTo(0); }
      else { setShowDrawer(true); }
    },
    leftMid: () => {
      if (apm.isLandscape) { }
      else { pageHandler.jumpTo(apm.indexes.cPageI - 1); }
    },
    leftBot: () => {
      if (apm.isLandscape) { pageHandler.jumpTo(apm.pages.length - 1); }
      else { pageHandler.jumpTo(0); }
    }
  }

  const swipeHandler = useSwipeable({
    onSwipedUp: (eventData) => { 
      if(!apm.isLandscape) return;
      pageHandler.jumpTo(apm.indexes.cPageI + 1);
    },
    onSwipedDown: (eventData) => { 
      if(!apm.isLandscape) return;
      pageHandler.jumpTo(apm.indexes.cPageI - 1);
    },
    onSwipedLeft: (eventData) => { 
      if(apm.isLandscape) return; 
      pageHandler.jumpTo(apm.indexes.cPageI + 1); 
    },
    onSwipedRight: (eventData) => { 
      if(apm.isLandscape) return; 
      pageHandler.jumpTo(apm.indexes.cPageI - 1); 
    }
  });

  //#endregion
  const showGuide = IS_PUBLIC && apm.indexes !== null && apm.indexes.cPageI === 0;
  const overlayStyle = getOverlayStyle(showGuide);
  const buttonStyle = {
    side: {
      ...overlayStyle.button,
      minHeight: 0.25 * window.innerHeight
    },
    mid: {
      ...overlayStyle.button,
      minHeight: 0.5 * window.innerHeight
    }
  }

  if (apm.indexes === null || apm.pages.length === 0) { return (<></>); }

  console.log('currentApm', apm);

  const currentPageInfo = apm.indexes.triad === "zenpen" ? apm.pages[apm.indexes.zenpenIndex]
    : apm.indexes.triad === "chuhen" ? apm.pages[apm.indexes.chuhenIndex]
      : apm.indexes.triad === "kouhen" ? apm.pages[apm.indexes.kouhenIndex]
        : null;

  const zenpenPage = <PageDisplay
    id="zenpen-page"
    albumPath={apm.path}
    pageInfo={apm.pages[apm.indexes.zenpenIndex]}
    queue={apm.indexes.triad === "zenpen" ? "q0" : apm.indexes.triad === "kouhen" ? "q1" : "q2"}
    isLandscape={apm.isLandscape}
    type={props.type}
    DEBUGPageTriad="zenpen"
  />;

  const chuhenPage = <PageDisplay
    id="chuhen-page"
    albumPath={apm.path}
    pageInfo={apm.pages[apm.indexes.chuhenIndex]}
    queue={apm.indexes.triad === "chuhen" ? "q0" : apm.indexes.triad === "zenpen" ? "q1" : "q2"}
    isLandscape={apm.isLandscape}
    type={props.type}
    DEBUGPageTriad="chuhen"
  />;

  const kouhenPage = <PageDisplay
    id="kouhen-page"
    albumPath={apm.path}
    pageInfo={apm.pages[apm.indexes.kouhenIndex]}
    queue={apm.indexes.triad === "kouhen" ? "q0" : apm.indexes.triad === "chuhen" ? "q1" : "q2"}
    isLandscape={apm.isLandscape}
    type={props.type}
    DEBUGPageTriad="kouhen"
  />;

  const menu = <EditModalTrigger onVisible={props.onOpenEditModal}></EditModalTrigger>;

  return (
    <>
      <div className="overlay overlay-reader" style={{ display: props.isOpen ? "block" : "none", zIndex: 1 }}>
        {/* <div className="overlay-content"> */}
        {/* <div style={{ position: "relative", top: "0%", textAlign: "center", height: window.innerHeight, width: window.innerWidth }}> */}
        <div style={{ height: "100vh", position: "absolute", left: 0, top: 0 }}>
          {zenpenPage}
          {chuhenPage}
          {kouhenPage}
        </div>
        <div className="overlay" style={{ zIndex: 2 }}>
          <div className="overlay-content">
            <span className="with-shadow">{currentPageInfo.name}</span>
            <div style={{ position: "fixed", bottom: "0px", width: "100%", textAlign: "center" }}>
              <span className="with-shadow">{apm.indexes.cPageI + 1}/{apm.pages.length}</span>
            </div>
          </div>
        </div>
        {!apm.includeDetail ? <></> : 
          <div className="overlay" style={{ zIndex: 3 }}>
            <div className="overlay-content">
              <Row>
                <Col span={6}><div style={buttonStyle.side}></div></Col>
                <Col span={12}><div style={buttonStyle.side}></div></Col>
                <Col span={6}>
                  <div className="with-shadow" style={{ textAlign:"left",...buttonStyle.side}}>
                    <div>{_helper.formatBytes(currentPageInfo.size, 0)}</div>
                    <div>{_helper.formatDate(currentPageInfo.createDate)}</div>
                    <div>{_helper.formatDate(currentPageInfo.updateDate)}</div>
                  </div>
                </Col>
              </Row>
            </div>
          </div>
        }
        <div className="overlay" style={{ zIndex: 4 }}>
          <div style={overlayStyle.content} {...swipeHandler}>
            <Row>
              <Col span={6}>
                <div onClick={buttonHandler.leftTop} style={buttonStyle.side}>
                  {
                    getGuideLabel(showGuide, "Browse Chapters")
                    //WARNING, getGuideLabel mechanism has not been updated to conform with rotation mechanic (isLandscape)
                  }
                </div>
              </Col>
              <Col span={12}>
                <div onClick={buttonHandler.midTop} style={buttonStyle.side}>
                  {getGuideLabel(showGuide, "Show File Info")}
                </div>
              </Col>
              <Col span={6}>
                <div onClick={buttonHandler.rightTop} style={buttonStyle.side}>
                  {getGuideLabel(showGuide, "Close Album")}
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={6}>
                <div onClick={buttonHandler.leftMid} style={buttonStyle.mid}>
                  {getGuideLabel(showGuide, "Previous Page")}
                </div>
              </Col>
              <Col span={12}>
                <Dropdown overlay={menu} trigger={['contextMenu']} disabled={false}>
                <div onClick={buttonHandler.midMid} style={buttonStyle.mid}>
                  {getGuideLabel(showGuide, "Context Menu")}
                </div>
                </Dropdown>
              </Col>
              <Col span={6}>
                <div onClick={buttonHandler.rightMid} style={buttonStyle.mid}>
                  {getGuideLabel(showGuide, "Next Page")}
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={6}>
                <div onClick={buttonHandler.leftBot} style={buttonStyle.side}>
                  {getGuideLabel(showGuide, "First Page")}
                </div>
              </Col>
              <Col span={12}>
                <div onClick={buttonHandler.midBot} style={buttonStyle.side} />
              </Col>
              <Col span={6}>
                <div onClick={buttonHandler.rightBot} style={buttonStyle.side}>
                  {getGuideLabel(showGuide, "Last Page")}
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>
      <ReaderModalContextMenu
        visible={showContextMenu}
        albumCm={props.albumCm}
        initialValue={apm.indexes.cPageI}
        pageName={currentPageInfo.name}
        type={props.type}

        onTierChange={albumHandler.tierChange}
        onRecount={() => { albumHandler.recount(); setShowContextMenu(false); }}
        onJump={(page) => { pageHandler.jumpTo(page); setShowContextMenu(false); }}
        onUndoJump={() => { pageHandler.jumpTo(apm.indexes.pPageI); setShowContextMenu(false); }}
        onRename={(newVal, oldVal) => { pageHandler.rename(newVal, oldVal); setShowContextMenu(false); }}
        onMove={(newDir) => { pageHandler.move(newDir); setShowContextMenu(false); }}
        onDelete={() => { pageHandler.delete(currentPageInfo); setShowContextMenu(false); }}
        onCancel={() => setShowContextMenu(false)}
      />
      <ReaderModalChapterDrawer
        visible={showDrawer}
        albumPath={apm.path}
        type={props.type}

        onClose={() => setShowDrawer(false)}
        onJumpToPage={pageHandler.jumpTo}
        onChapterDeleteSuccess={pageHandler.chapterDeleteSuccess}
        onChapterRenameSuccess={pageHandler.chapterRenameSuccess}
      />
    </>
  );
}

const getOverlayStyle = (isGrey) => {
  const result = {
    content: {
      position: 'relative',
      top: '0%',
      width: '100%',
      textAlign: 'center',
      backgroundColor: isGrey ? 'rgba(200, 200, 200, 0.3)' : 'rgba(0, 0, 0, 0)'
    },
    button: {
      position: 'relative',
      boxSizing: 'border-box',
      border: isGrey ? '1px solid black' : '0px'
    }
  };

  return result;
}

const getGuideLabel = (isGrey, text) => {
  return isGrey ?
    <span style={{ textAlign: "center", margin: "auto", textShadow: "-1px 0 black, 0 1px black, 1px 0 black, 0 -1px black" }}>
      {text}
    </span> :
    "";
}

function PageDisplay(props) {
  const styleByRotation = props.isLandscape ?
    {
      transform: "translatex(calc(50vw - 50%)) translatey(calc(50vh - 50%)) rotate(90deg)",
      width: "100vh",
      height: "100vw",
    } :
    {
      transform: "translatex(calc(50vw - 50%)) translatey(calc(50vh - 50%))",
      width: "100vw",
      height: "100vh"
    };

  const styleShow = {
    objectFit: "contain",
    ...styleByRotation
  };
  const styleHide = { objectFit: "contain", height: 0, width: 0, position: "absolute" };
  //const styleShow = { height: 300, width: 300 };
  //const styleHide = { height: 150, width: 150 };

  console.log('pageDisplay', props);

  if (IsVideo(props.pageInfo.extension)) {
    return (
      <video
        autoPlay loop
        id={props.id}
        style={props.queue === "q0" ? styleShow : styleHide}
        src={props.queue === "q0" ? _uri.StreamPage(`${props.albumPath}\\${props.pageInfo.uncPathEncoded}`, props.type) : ""}
      >
        video not showing
      </video>
    )
  }

  return (
    <img
      id={props.id}
      style={props.queue === "q0" ? styleShow : styleHide}
      src={props.queue === "q0" || props.queue === "q1" ? _uri.StreamPage(`${props.albumPath}\\${props.pageInfo.uncPathEncoded}`, props.type) : loadingImg}
      alt="Not loaded"
    />
  );
}

function IsVideo(extension) {
  if (extension === ".webm" || extension === ".mp4") {
    return true;
  }
  return false;
}

function EditMenuInbetweener(props) {
  useEffect(() => {
    props.onVisible();
  },[props]);
  return (<></>)
}

export default withPageManager(withMyAlert(ReaderModal));