import React, { useState, useEffect } from 'react';

import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { FileBrowser, FileContextMenu, FileList, FileNavbar, FileToolbar, ChonkyActions, defineFileAction, ChonkyIconName } from 'chonky';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { TouchBackend } from 'react-dnd-touch-backend';
import moment from 'moment';

import withPageManager from '../_shared/HOCs/withPageManager';
import withMyAlert from '../_shared/HOCs/withMyAlert';
import TextDialog from '../_shared/Modals/TextDialog';
import { IsMobile } from '../_utils/Display';
import * as _uri from '../_utils/UriHelper';
import * as _helper from '../_utils/Helper';

const { confirm } = Modal;
const axios = require('axios').default;

function FileManagement(props) {
  const chonkyFileActions = [
    defineFileAction({
      id: "custom_rename",
      requiresSelection: true,
      button: {
        name: "R",
        toolbar: true,
        contextMenu: true,
        icon: ChonkyIconName.code
      }
    }),
    defineFileAction({
      id: "custom_delete",
      requiresSelection: true,
      button: {
        name: "D",
        toolbar: true,
        contextMenu: true,
        icon: ChonkyIconName.trash
      }
    }),
    defineFileAction({
      id: "custom_move",
      requiresSelection: true,
      button: {
        name: "M",
        toolbar: true,
        contextMenu: true,
        icon: ChonkyIconName.folder
      }
    }),
    ChonkyActions.EnableGridView,
    ChonkyActions.EnableListView,
    ChonkyActions.UploadFiles,
    ChonkyActions.CreateFolder
  ];

  const determineActionToExecute = (data) => {
    if (data.id === "custom_delete") action.delete(data);
    else if (data.id === "custom_rename") action.rename(data);
    else if (data.id === "custom_move") console.log(data.id);
    else if (data.id === ChonkyActions.EndDragNDrop.id) action.dragNDrop(data);
    else if (data.id === ChonkyActions.UploadFiles.id) console.log(data.id);
    else if (data.id === ChonkyActions.OpenFiles.id) action.openFolder(data);
    else if (data.id === ChonkyActions.OpenParentFolder.id) console.log(data.id);
    else if (data.id === ChonkyActions.CreateFolder.id) action.createFolder(data);
  };

  const action = {
    createFolder: (data) =>{
      setTextDialog({
        visible: true,
        btnText: 'Create Folder',
        initialValue: '',
        onOk: (val, oldVal) => {
          console.log('folder name', val);
          setViewModel((prev) => {
            if (viewModel.subDir !== null) {
              props.popError("Invalid Operation", "Creating folder inside a folder is not allowed");
            }
            prev.fsNodes.unshift({
              name: val,
              size: 0,
              nodeType: 0,
              uncPathEncoded: null,
              childs: []
            });
            return ({
              ...prev
            });
          });
        }
      });
    },
    openFolder: (data)=>{
      if (!data.payload.targetFile.isDir) { return; }

      const targetSubDir = data.payload.targetFile.name !== "Root" ?
        data.payload.targetFile.name :
        null;
      const newDirChains = targetSubDir !== null ?
        [...viewModel.dirChains, { id: '1', name: targetSubDir, isDir: true }] :
        rootChain;

      setViewModel((prevViewModel) => {
        prevViewModel.subDir = targetSubDir;
        prevViewModel.dirChains = newDirChains;
        return ({
          ...prevViewModel
        });
      });
    },
    delete: (data) => {
      const cModel = data.state.selectedFiles[0];
      const subDir = viewModel.dirChains.find(dir => dir.id === "1")?.name;

      const delObj = (() => {
        const alRelPath = cModel.isDir ? cModel.name : _helper.pathJoin([subDir, cModel.name], '\\');

        return {
          albumPath: viewModel.albumPath,
          alRelPath: alRelPath
        };
      })();

      props.deletePage(delObj,
        (response) => {
          console.log('delete success', response);
          const newFsNodes = (() => {
            if(cModel.isDir || subDir == null){
              return viewModel.fsNodes.filter(n => n.name !== cModel.name);
            }
            else{
              viewModel.fsNodes.forEach(n => {
                if (n.name === subDir) {
                  n.childs = n.childs.filter(c => c.name !== cModel.name);
                }
              });
              return viewModel.fsNodes;
            }
          })();

          setViewModel((prevState) => ({
            ...prevState,
            fsNodes: newFsNodes
          }));
        },
        (error) => {
          props.popApiError(error);
        });
    },
    rename: (data) => {
      if (data.state.selectedFiles.length === 0) { return; }
      setTextDialog({
        visible: true,
        btnText: 'Rename',
        initialValue: data.state.selectedFiles[0].name,
        onOk: (val, oldVal) => {
          const srcAlRelPath = _helper.pathJoin([viewModel.subDir, oldVal], '\\');
          const dstAlRelPath = _helper.pathJoin([viewModel.subDir, val], '\\');

          const movObj = {
            overwrite: false,
            src: {
              albumPath: viewModel.albumPath,
              alRelPath: srcAlRelPath,
            },
            dst: {
              albumPath: viewModel.albumPath,
              alRelPath: dstAlRelPath
            }
          };

          props.movePage(movObj, 
            (response) => {
              afterAction.move(movObj);
            },(error) => {
              props.popApiError(error);
            });
        }
      });
    },
    dragNDrop: (data) => {
      if (data.payload.draggedFile.isDir) { return; }
      if (!data.payload.destination.isDir) { return; }

      const fileName = data.payload.draggedFile.name;
      const srcAlRelPath = _helper.pathJoin([viewModel.subDir, fileName], '\\');
      const dstDir = data.payload.destination.name !== "Root" ? data.payload.destination.name : null;
      const dstAlRelPath = _helper.pathJoin([dstDir, fileName], '\\');

      const movObj = {
        overwrite: false,
        src: {
          albumPath: viewModel.albumPath,
          alRelPath: srcAlRelPath
        },
        dst: {
          albumPath: viewModel.albumPath,
          alRelPath: dstAlRelPath
        }
      };

      props.movePage(movObj, 
        (response) => {
          afterAction.move(movObj);
        },(error) => {
          props.popApiError(error);
        });
    },
  };

  const afterAction = {
    move: (movObj) => {
      const albumPath = movObj.src.albumPath;
      const srcObj = splitMoveModel(movObj.src.alRelPath);
      const dstObj = splitMoveModel(movObj.dst.alRelPath);

      const srcFsNode = (() => {
        if(srcObj.subDir === null){
          const lv1Idx = viewModel.fsNodes.findIndex(n => n.name === srcObj.fileName);
          return viewModel.fsNodes.splice(lv1Idx, 1)[0];
        }
        else{
          const lv1Idx = viewModel.fsNodes.findIndex(n => n.name === srcObj.subDir);
          const lv2Idx = viewModel.fsNodes[lv1Idx].childs.findIndex(n => n.name === srcObj.fileName);
          return viewModel.fsNodes[lv1Idx].childs.splice(lv2Idx, 1)[0];
        }
      })();

      const modifiedSrcFsNode = {
        ...srcFsNode,
        name: dstObj.fileName
      };

      const removedNode = (() =>{
        //if dst node exist it means overwrite, remove it
        if(dstObj.subDir === null){
          const lv1Idx = viewModel.fsNodes.findIndex(n => n.name === dstObj.fileName);
          if(lv1Idx !== -1) return viewModel.fsNodes.splice(lv1Idx, 1)[0];
        }
        else{
          const lv1Idx = viewModel.fsNodes.findIndex(n => n.name === dstObj.subDir);
          const lv2Idx = viewModel.fsNodes[lv1Idx].childs.findIndex(n => n.name === dstObj.fileName);
          if(lv2Idx !== -1) return viewModel.fsNodes[lv1Idx].childs.splice(lv2Idx, 1)[0];
        }
      })();

      const targetParentIdx = (() => {
        if(dstObj.subDir !== null){
          return viewModel.fsNodes.findIndex(n => n.name === dstObj.subDir);
        }
        return null;
      })();

      if(targetParentIdx !== null){
        viewModel.fsNodes[targetParentIdx].childs.push(modifiedSrcFsNode);
        viewModel.fsNodes[targetParentIdx].childs.sort(compare);
      }
      else{
        viewModel.fsNodes.push(modifiedSrcFsNode);
        viewModel.fsNodes.sort(compare);
      }

      setViewModel((prev) => {
        return ({
          ...prev
        });
      });

      function splitMoveModel(alRelPath){
        const arr = alRelPath.split('\\');
        if(arr.length === 2){
          return {
            subDir: arr[0],
            fileName: arr[1]
          };
        }
        else{
          return {
            subDir: null,
            fileName: arr[0]
          };
        }
      }
      function compare(a, b) {
        if (a.nodeType === b.nodeType) { //Make sure folders always come before files
          var nameA = a.name.toLowerCase(), nameB = b.name.toLowerCase();
          if (nameA < nameB) //sort string ascending
            return -1;
          if (nameA > nameB)
            return 1;
          return 0; //default return value (no sorting)
        }
        return a.nodeType > b.nodeType ? 1 : -1;
      }
    }
  }

  const defaultTextDialog = { visible: false, initialValue: 'ad', btnText: null };
  const [textDialog, setTextDialog] = useState(defaultTextDialog);

  const rootChain = [{ id: '0', name: 'Root', isDir: true }];
  const [viewModel, setViewModel] = useState({
    albumPath: null,
    title: '',
    subDir: null,
    fsNodes: [],
    dirChains: rootChain
  });

  useEffect(() => {
    if (props.path === "" || props.path === null || props.path === undefined) { return; }

    axios.get(_uri.GetAlbumFsNodes(props.type, props.path))
      .then((response) => {
        setViewModel({
          albumPath: props.path,
          title: response.data.title,
          subDir: null,
          fsNodes: response.data.fsNodes,
          dirChains: rootChain
        });

        console.log(response.data)
      })
      .catch((error) => {
        props.popApiError(error);
      });

  }, [props.path]);

  const [chonkyModels, setChonkyModels] = useState([]);
  useEffect(() => {
    const targetFsNodes = viewModel.subDir !== null ?
      viewModel.fsNodes.find(n => n.name === viewModel.subDir).childs :
      viewModel.fsNodes;

    const newModels = targetFsNodes.map((n, index) => {
      return {
        id: index + 1,
        name: n.name,
        isDir: n.nodeType === 0,
        thumbnailUrl: n.nodeType === 1 ? _uri.StreamResizedImage(n.uncPathEncoded, 150) : null,
        size: n.size,
        childrenCount: n.childs?.length,
        selectable: true,
        draggable: n.nodeType !== 0
      }
    });

    setChonkyModels(newModels);
  }, [viewModel]);

  const dndBackend = IsMobile() ? TouchBackend : HTML5Backend;

  return (
    <>
      <div style={{
        width: "100%",
        padding: "0px 8px 0px 8px",
        borderTop: "1px solid grey",
        borderBottom: "1px solid grey",
        marginBottom: "2px"
      }}>
        <h3>{viewModel.title}</h3>
      </div>
      <div style={{ height: "80vh" }}>
        <DndProvider backend={dndBackend}>
          <FileBrowser
            darkMode={true}
            //disableSelection={true}
            files={chonkyModels} folderChain={viewModel.dirChains} fileActions={chonkyFileActions}
            disableDragAndDropProvider={true}
            disableDefaultFileActions={true}
            onFileAction={determineActionToExecute}
          >
            <FileNavbar />
            <FileToolbar />
            <FileList />
            <FileContextMenu />
          </FileBrowser>
        </DndProvider>
      </div>
      <TextDialog
        visible={textDialog.visible} onClose={() => setTextDialog(defaultTextDialog)}
        initialValue={textDialog.initialValue}
        onOk={textDialog.onOk}
        btnText={textDialog.btnText}
      />
    </>
  );
}

export default withPageManager(withMyAlert(FileManagement));