import React, { useState, useEffect } from 'react';

import { Row, Col, Drawer, Modal, Menu, Dropdown } from 'antd';
import {
  EditOutlined,
  DeleteOutlined,
  ExclamationCircleOutlined
} from '@ant-design/icons';

import MyInputStar from '../Editors/MyInputStar';
import withMyAlert from '../HOCs/withMyAlert';
import withPageManager from '../HOCs/withPageManager';
import TextDialog from '../Modals/TextDialog';
import * as _uri from '../../_utils/UriHelper';

const { confirm } = Modal;
const axios = require('axios').default;

function ReaderModalChapterDrawer(props) {
  const [chapters, setChapters] = useState([]);
  const [renameModal, setRenameModal] = useState({
    visible: false,
    initialValue: '',
    onOk: (value, initialValue) => {
      console.log('rename ok', value, initialValue);

      const movObj = {
        overwrite: false,
        src: {
          albumPath: props.albumPath,
          alRelPath: initialValue
        },
        dst: {
          albumPath: props.albumPath,
          alRelPath: value
        }
      };

      props.movePage(movObj, 
        (response) => {
          setChapters((prev) => {
            const updateIndex = prev.findIndex(a => a.title === movObj.src.alRelPath);
            prev[updateIndex].title = movObj.dst.alRelPath;

            return ([
              ...prev
            ]);
          });

          props.onChapterRenameSuccess();
        },(error) => {
          props.popApiError(error);
        });
    }
  });

  const updateChapterTier = (param) => {
    axios.post(_uri.UpdateAlbumChapter(props.type), param)
        .then(function (response) {
          setChapters((prev) => {
            const updateIndex = prev.findIndex(a => a.title === param.chapterName);
            
            prev[updateIndex].tier = param.tier;

            return ([
              ...prev
            ]);
          });

          props.onChapterRenameSuccess();
        })
        .catch(function (error) {
          props.popApiError(error);
        });
  };

  useEffect(() => {
    if (props.albumPath === undefined) { return; }
    axios.get(_uri.GetAlbumChapters(props.type, props.albumPath))
      .then(function (response) {
        setChapters(response.data);
      })
      .catch(function (error) {
        props.popApiError(error);
      });
  }, [props.albumPath]);

  const handler = {
    rename: (chapterTitle) => {
      setRenameModal(prev => {
        prev.visible = true;
        prev.initialValue = chapterTitle;
        return{ ...prev };
      });
    },
    delete: (chapterTitle) => {
      confirm({
        title: `Delete chapter ${chapterTitle}?`,
        icon: <ExclamationCircleOutlined />,
        okText: 'Yes',
        okType: 'danger',
        cancelText: 'No',
        onOk() {
          axios.delete(_uri.DeleteAlbumChapter(props.type, props.albumPath, chapterTitle))
            .then(function (response) {
              console.log("newPageCount", response.data);
              props.onChapterDeleteSuccess(response.data);
              setChapters(
                chapters.filter(c => c.title !== chapterTitle)
              );
            })
            .catch(function (error) {
              props.popApiError(error);
            });
        },
        onCancel() {
        },
      });
    },
    tierChange:(chapterTitle, value) => {
      updateChapterTier({ 
        albumPath:props.albumPath, 
        chapterName:chapterTitle, 
        tier:value 
      });
    }
  };

  return (
    <Drawer
      placement="left"
      closable={false}
      onClose={props.onClose}
      visible={props.visible}
    >
      <div style={{ padding: "10px" }}>
        {chapters.map((chapter, index) => (
          <Row style={{ marginBottom: "5px" }} key={chapter.title}>
            <Dropdown overlay={
              <Menu>
                <Menu.Item key="1" onClick={() => handler.rename(chapter.title)}><EditOutlined />Rename</Menu.Item>
                <Menu.Item key="2" onClick={() => handler.delete(chapter.title)}><DeleteOutlined />Delete</Menu.Item>
              </Menu>}
              trigger={['contextMenu']}
            >
              <Col span={10} onClick={() => props.onJumpToPage(chapter.pageIndex)}>
                <img
                  style={{ objectFit: "contain", width: "100%", maxHeight: "128px", border: "1px solid white" }}
                  src={_uri.StreamResizedImage(chapter.pageUncPath, 150, props.type)}
                  alt="img"
                >
                </img>
              </Col>
            </Dropdown>
            <Col span={14}>
              <Row>
                <Col span={24}>
                  <span style={{ color: "white" }}>{chapter.title}</span>
                </Col>
                <Col span={24} style={{ paddingLeft:'10px' }}>
                  <MyInputStar  value={chapter.tier} colSpan={{ label:0, control:24 }} onChange={(label, value) => { handler.tierChange(chapter.title, value); }}  />
                </Col>
              </Row>
            </Col>
          </Row>
        ))};
      </div>
      <TextDialog
        visible={renameModal.visible} 
        onClose={() => setRenameModal(prev => { prev.visible = false; return {...prev}; })}
        initialValue={renameModal.initialValue}
        onOk={renameModal.onOk}
      />
    </Drawer>
  );
}

export default withPageManager(withMyAlert(ReaderModalChapterDrawer));