import React, { useState, useEffect } from 'react';

import { Row, Col, Button, Checkbox } from 'antd';
import useSWR from 'swr';

import MyInputTextbox from '../_shared/Editors/MyInputTextbox';
import MyInputStar from '../_shared/Editors/MyInputStar';
import MyInputRadio from '../_shared/Editors/MyInputRadio';
import MyInputCheck from '../_shared/Editors/MyInputCheck';
import MyInputMulticheck from '../_shared/Editors/MyInputMulticheck';
import withMyAlert from '../_shared/HOCs/withMyAlert';

import * as _helper from '../_utils/Helper';
import * as _uri from '../_utils/UriHelper';

const lodash = require('lodash/array');
const axios = require('axios').default;

function EditModal(props) {
  const { data: albumInfo, error: aiError } = useSWR(_uri.GetAlbumInfo());

  const [albumVm, setAlbumVm] = useState({
    path: "", //not updated
    pageCount: 0, //not updated
    lastPageIndex: 0, //not updated
    album: {
      title: "",
      category: "Manga",
      orientation: "Portrait",
      artists: [],
      tags: [],
      languages: [],
      flags: [],
      tier: 0,
      cover: "",
      isWip: false,
      isRead: false,
      entryDate: null
    }
  });

  useEffect(() => {
    if (props.albumPath === undefined) { return; }

    axios.get(_uri.GetAlbumVm(props.type, props.albumPath))
      .then(function (response) {
        setAlbumVm(response.data);
      })
      .catch(function (error) {
        props.popApiError(error);
      });

  }, [props.albumPath]);

  const handlers = {
    albumVmChange: function (label, value) {
      let newAlbumVm = { ...albumVm };
      let cleanedValue = value;
      if (label === "Artists") {
        cleanedValue = value.split(",");
      }
      else if (label === "Tags" || label === "Languages") {
        cleanedValue = albumVm.album[_helper.firstLetterLowerCase(label)];
        if (cleanedValue.includes(value)) {
          cleanedValue = lodash.pull(cleanedValue, value);
        }
        else {
          cleanedValue.push(value);
        }
      }
      newAlbumVm.album[_helper.firstLetterLowerCase(label)] = cleanedValue;
      setAlbumVm(newAlbumVm);
    },
    ok: function () {
      props.onOk(albumVm);
    },
    cancel: function () {
      props.onCancel();
    },
    editFiles: function () {
      props.onEditFiles(albumVm);
    },
    delete: function () {
      props.onDelete(albumVm);
    },
    refresh: function () {
      props.onRefresh(albumVm);
    }
  }

  const temp = (
    <div className="ant-modal-root">
      <div className="ant-modal-mask"></div>
      <div tabIndex={-1} className="ant-modal-wrap" role="dialog" aria-labelledby="rcDialogTitle0" onClick={handlers.cancel}>
        <div role="document" className="my-ant-modal" onClick={(e) => e.stopPropagation()}>
          <div className="ant-modal-content">
            <button onClick={handlers.cancel} type="button" aria-label="Close" className="ant-modal-close">
              <span className="ant-modal-close-x">
                <i aria-label="icon: close" className="anticon anticon-close ant-modal-close-icon">
                  <svg viewBox="64 64 896 896" focusable="false" className="" data-icon="close" fill="currentColor" aria-hidden="true" style={{ height: "1em", width: "1em" }}>
                    <path d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 0 0 203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z">
                    </path>
                  </svg>
                </i>
              </span>
            </button>
            <div className="my-ant-modal-header">
              <div className="my-ant-modal-title" id="rcDialogTitle0">Edit Metadata</div>
            </div>
            <div className="my-ant-modal-body">
              {aiError ? "album Info Fetching error" : !albumInfo ? "loading..." : (
                <>
                  <MyInputTextbox label="Title" row={3} value={albumVm.album.title} onChange={handlers.albumVmChange} />
                  <MyInputTextbox label="Artists" value={albumVm.album.artists.join()} onChange={handlers.albumVmChange} />
                  <MyInputRadio label="Category" value={albumVm.album.category} items={albumInfo.categories} onChange={handlers.albumVmChange} />
                  <MyInputRadio label="Orientation" value={albumVm.album.orientation} items={albumInfo.orientations} onChange={handlers.albumVmChange} />
                  <MyInputMulticheck label="Tags" value={albumVm.album.tags} items={albumInfo.tags} onChange={handlers.albumVmChange} />
                  <MyInputMulticheck label="Languages" value={albumVm.album.languages} items={albumInfo.languages} onChange={handlers.albumVmChange} />
                  <MyInputTextbox label="Note" value={albumVm.album.note} onChange={handlers.albumVmChange} showClearButton={true} />
                  <MyInputStar label="Tier" value={albumVm.album.tier} onChange={handlers.albumVmChange} />
                  <Row gutter={[0, 8]}>
                    <Col span={6}>
                      <label className='my-editor-label'>Flags</label>
                    </Col>
                    <Col sm={18}>
                      <Row gutter={2}>
                        <Col span={6}>
                          <Checkbox onChange={(event) => handlers.albumVmChange('IsWip', event.target.checked)} checked={albumVm.album.isWip}>
                            <span style={{ color: "white" }}>WIP</span>
                          </Checkbox>
                        </Col>
                        <Col span={6}>
                          <Checkbox onChange={(event) => handlers.albumVmChange('IsRead', event.target.checked)} checked={albumVm.album.isRead}>
                            <span style={{ color: "white" }}>Read</span>
                          </Checkbox>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </>
              )}
            </div>
            <div className="my-ant-modal-footer">
              <Row>
                <Col span={5}>
                  <Button onClick={handlers.delete} type="danger" style={{ width: "100%" }}>
                    <span className='my-editor-button-label'>Delete</span>
                  </Button>
                </Col>
                <Col span={1}></Col>
                <Col span={5}>
                  <Button onClick={handlers.refresh} type="ghost" style={{ width: "100%", color: "white" }}>
                    <span className='my-editor-button-label'>Refresh</span>
                  </Button>
                </Col>
                <Col span={2}></Col>
                <Col span={5}>
                  <Button onClick={handlers.editFiles} type="ghost" style={{ width: "100%", color: "white" }}>
                    <span className='my-editor-button-label'>Files</span>
                  </Button>
                </Col>
                <Col span={1}></Col>
                <Col span={5}>
                  <Button onClick={handlers.ok} type="primary" style={{ width: "100%" }}>
                    <span className='my-editor-button-label'>Save</span>
                  </Button>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return (<>{props.isOpen ? temp : ""}</>);
}

export default withMyAlert(EditModal);