
export function firstLetterLowerCase(string) {
  return string.charAt(0).toLowerCase() + string.slice(1);
}

export function apiErrorHandler(error) {
  if (typeof error.response !== undefined) {
    alert(JSON.stringify(error.response, null, "\t"))
  }
  else {
    alert(JSON.stringify(error, null, "\t"));
  }
}

export function getPercent100(value, maxValue) {
  if(maxValue === 0) return 0;

  let fraction = (value / maxValue) * 100;
  let result = fraction <= 100 ? fraction : 100;
  return result;
}

export function contains(target, pattern) {
  var value = 0;
  pattern.forEach(function (word) {
    value = value + target.includes(word);
  });
  return (value === 1);
}

export function clamp(number, min, max) {
  return Math.max(min, Math.min(number, max));
}

export function clickReport(event) {
  const result = {
    ecy: event.clientY,
    epy: event.pageY,
    ety: event.touches && event.touches[0] ? event.touches[0].clientY : null,
    echy: event.changedTouches && event.changedTouches[0] ? event.changedTouches[0].clientY : null
  };
  return result;
}

export function pathJoin(parts, sep){
  var validParts = parts.filter(e => { return e != null && e !== "" });
  var separator = sep || '/';
  var replace   = new RegExp(separator+'{1,}', 'g');
  
  return validParts.join(separator).replace(replace, separator);
}

export function formatBytes(bytes, decimals = 2){
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}

export function formatDate(date){
  return new Date(date).toLocaleString();
}