import React, { useState, useEffect } from 'react';
import { withRouter, Link } from "react-router-dom";

import { Layout, Menu, Drawer, Button, Row, Col, Modal } from 'antd';
import {
  DesktopOutlined, TeamOutlined, PartitionOutlined, TagsOutlined,
  PlusOutlined, MinusOutlined, ConsoleSqlOutlined, SearchOutlined,  
  LaptopOutlined, SettingOutlined, MenuOutlined
} from '@ant-design/icons';
import TextArea from 'antd/lib/input/TextArea';
import queryString from 'query-string';
import useSWR, { SWRConfig } from 'swr';

import SelfComps from './SelfComps/SelfComps';
import Albums from './Albums/Albums';
import Genres from './Genres/Genres';
import FileManagement from './FileManagement/FileManagement';
import Setting from './Setting/Setting';
import withMyAlert from "./_shared/HOCs/withMyAlert";
import * as Helper from "./_utils/Helper";
import * as _uri from "./_utils/UriHelper";
import { DesktopOnly, MobileOnly } from './_utils/Display';
import { IS_PUBLIC, API_URL, STORAGE_URL, SAS_TOKEN } from "./_utils/Config";

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;


function AppMasterPage(props) {
  const [showDrawer, setShowDrawer] = useState(false);
  const [collapseSider, setCollapseSider] = useState(false);

  const [selectedTiers, setSelectedTiers] = useState([]);
  useEffect(() =>{
    const storedSelectedTiers = window.localStorage.getItem('selectedTiers');
    if(storedSelectedTiers != null)
      setSelectedTiers(JSON.parse(storedSelectedTiers));
    else
      setSelectedTiers(['N','0','1','2','3']);
  },[]);

  const handleTierChange = (tier) => {
    const newTier = selectedTiers.includes(tier)
      ? selectedTiers.filter(e => e !== tier)
      : [...selectedTiers, tier];

    setSelectedTiers(newTier);

    window.localStorage.setItem('selectedTiers', JSON.stringify(newTier));
  }

  const querStr = queryString.parse(props.location.search).query ?? "";
  const pageStr = queryString.parse(props.location.search).page ?? 0;
  const rowStr = queryString.parse(props.location.search).row ?? 0;
  const pathStr = queryString.parse(props.location.search).path ?? "";

  const queryParts = {
    query: querStr,
    page: pageStr,
    row: rowStr,
    path: pathStr
  };

  const { page, selectedMenu } = (() => {
    function compose(page, selectedMenu) {
      return {
        page: page,
        selectedMenu: selectedMenu
      };
    }
    if(props.page === "Sc"){
      return compose(
        <SelfComps queryParts={queryParts} />
        , '1');
    }
    else if (props.page === "Albums") {
      return compose(
        <Albums queryParts={queryParts} selectedTiers={selectedTiers} />
        , '2');
    }
    else if (props.page === "Artists") {
      return compose(
        <Genres page={props.page} history={props.history} />
        , '3');
    }
    else if (props.page === "Genres") {
      return compose(
        <Genres page={props.page} history={props.history} />
        , '4');
    }
    else if (props.page === "FileManagement") {
      const path = queryString.parse(props.location.search).path ?? "";
      return compose(<FileManagement path={path} />, '');
    }
    else if (props.page === "Setting") {
      return compose(
        <Setting page={props.page} />
        , '5');
    }
  })();

  const queryHandler = {
    search: function (query) {
      queryHandler.goToAlbumList(query);
    },
    plus: function (tagName) {
      const targetQueryPlus = "tag:" + tagName;
      const targetQueryMinus = "tag!" + tagName;

      const currentQueries = querStr !== "" ? querStr.split(',') : [];
      let newQueries = currentQueries.filter((item, index) => !(item === targetQueryPlus || item === targetQueryMinus));
      newQueries.push(targetQueryPlus);
      const newQueryString = newQueries.join(',');

      queryHandler.goToAlbumList(newQueryString);
    },
    minus: function (tagName) {
      const targetQueryPlus = "tag:" + tagName;
      const targetQueryMinus = "tag!" + tagName;

      const currentQueries = querStr !== "" ? querStr.split(',') : [];
      let newQueries = currentQueries.filter((item, index) => !(item === targetQueryPlus || item === targetQueryMinus));
      newQueries.push(targetQueryMinus);
      const newQueryString = newQueries.join(',');

      queryHandler.goToAlbumList(newQueryString);
    },
    goToAlbumList: function (query) {
      let currentUrlParams = new URLSearchParams(window.location.search);
      currentUrlParams.set('query', query);
      props.history.push("Albums?" + currentUrlParams.toString());
    }
  }

  const styleMenuItem = { fontWeight:600 };

  const menuContent = (
    <>
      <div className="logo">
        {/* <span>{props.page === "Albums" ? albumCount : ''}</span> */}
      </div>
      <QueryEditor query={querStr} onOk={(query) => queryHandler.goToAlbumList(query)} />
      <Menu theme="dark" defaultSelectedKeys={[selectedMenu]} defaultOpenKeys={["sub1"]} mode="inline">
        {!IS_PUBLIC ? 
          <Menu.Item key="1">
            <Link to="/sc">
              <LaptopOutlined />
              <span style={styleMenuItem}>Self Comp</span>
            </Link>
          </Menu.Item>
        : ''}
        <Menu.Item key="2">
          <Link to="/albums">
            <DesktopOutlined />
            <span style={styleMenuItem}>Albums</span>
          </Link>
        </Menu.Item>
        <Menu.Item key="3">
          <Link to="/artists">
            <TeamOutlined />
            <span style={styleMenuItem}>Artists</span>
          </Link>
        </Menu.Item>
        <Menu.Item key="4">
          <Link to="/genres">
            <PartitionOutlined />
            <span style={styleMenuItem}>Genres</span>
          </Link>
        </Menu.Item>
        <SubMenu key="sub1" title={
            <>
              <TagsOutlined />
              <span style={styleMenuItem}>Tags</span>
            </>
          }
        >
          <TagMenuContainer onPlus={queryHandler.plus} onMinus={queryHandler.minus} querStr={querStr} />
        </SubMenu>
        <Menu.Item key="5">
          <Link to="/setting">
            <SettingOutlined />
            <span style={styleMenuItem}>Setting</span>
          </Link>
        </Menu.Item>
      </Menu>
    </>
  );

  return (
    <SWRConfig value={{ revalidateOnFocus: false, fetcher: (uri) => fetch(uri).then(res => res.json()) }}>
      <Layout style={{ minHeight: '100vh' }}>
        <MobileOnly>
          <Layout className="mobile-header">
            <Row>
              <Col span={12}></Col>
              <Col span={10}>
                {!IS_PUBLIC ? <TierChecklist onTierChange={handleTierChange} selectedTiers={selectedTiers} /> : <></> }
              </Col>
              <Col span={2} className="hamburger-container" onClick={() => setShowDrawer(true)}>
                <MenuOutlined className="hamburger" />
              </Col>
            </Row>
          </Layout>
          <Drawer
            placement="right"
            closable={false}
            onClose={() => setShowDrawer(false)}
            visible={showDrawer}
          >
            {menuContent}
          </Drawer>
        </MobileOnly>
        <DesktopOnly>
          <Sider collapsible collapsed={collapseSider} onCollapse={(event) => setCollapseSider(event)}>
            {menuContent}
          </Sider>
        </DesktopOnly>

        <Layout className="site-layout">
          <Content>
            <MobileOnly>
              <div style={{ paddingTop: "5x" }}>Padding for mobile</div>
            </MobileOnly>
            <div className="site-layout-background" style={{ paddingTop: 25, paddingLeft: 10, paddingRight: 10, minHeight: 360 }}>
              {page}
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>-----------------</Footer>
        </Layout>
      </Layout>
    </SWRConfig>
  );
}

export default withRouter(withMyAlert(AppMasterPage));

function TagMenuContainer(props) {
  const { data: tags, error } = useSWR(_uri.GetTagVMs());

  if (error) { return <div>error!</div>; }
  if (!tags) { return <div>loading...</div>; }

  return (
    <>
      {tags.map((tag, index) => (<TagMenuItem key={tag.name} tag={tag} onPlus={props.onPlus} onMinus={props.onMinus} selectStatus={getSelectStatus(tag.name, props.querStr)} />))}
    </>
  );

  function getSelectStatus(tagName, query) {
    const queryArr = query.split(',');
    const result = queryArr.filter((str) => str.toLowerCase().includes("tag:" + tagName.toLowerCase())).length > 0 ? "plus" :
      queryArr.filter((str) => str.toLowerCase().includes("tag!" + tagName.toLowerCase())).length > 0 ? "minus" :
        "none";

    return result;
  }
}

function TagMenuItem(props) {
  const encodedQuery = encodeURIComponent(props.tag.query);
  const plusBackground = props.selectStatus === "plus" ? "#1890ff" : "";
  const minusBackground = props.selectStatus === "minus" ? "#1890ff" : "";

  return (
    <li className="ant-menu-item" role="menuitem" style={{ paddingLeft: "24px", paddingRight: "0px", height:"28px", marginTop:"0px", marginBottom:"0px" }}>
      <Row>
        <Col span={5} style={{ textAlign: "center" }} onClick={() => props.onPlus(props.tag.name)}><PlusOutlined style={{ marginRight: "0px", backgroundColor: plusBackground }} /></Col>
        <Link to={"/albums?query=" + encodedQuery} >
          <Col span={14}>
            <span style={{ fontSize: "12px", opacity:0.8 }}>{props.tag.name}</span>
          </Col>
        </Link>
        <Col span={5} style={{ textAlign: "center" }} onClick={() => props.onMinus(props.tag.name)}><MinusOutlined style={{ marginRight: "0px", backgroundColor: minusBackground }} /></Col>
      </Row>
    </li>
  );
}

function QueryEditor(props) {
  const [visible, setVisible] = useState(false);
  const [query, setQuery] = useState("");

  useEffect(() => {
    setQuery(props.query);
  }, [props.query]);

  function handleOk() {
    setVisible(false);
    props.onOk(query);
  }

  return (
    <div style={{ paddingLeft: "24px" }}>
      <Button type="link" onClick={() => setVisible(true)} style={{ width: "90%", textAlign: "left", padding: "0px" }}>
        <ConsoleSqlOutlined /> 
        <span style={{ fontWeight:600 }}> Query Editor </span>
      </Button>
      <Modal
        visible={visible}
        onCancel={() => setVisible(false)}
        closable={false}
        footer={
          <Button type="primary" onClick={handleOk}>
            <SearchOutlined />Search
          </Button>
        }
      >
        <TextArea
          onChange={(event) => setQuery(event.target.value)}
          value={query}
        //autoSize={{ minRows: 2, maxRows: 5 }} //Enabling this will cause react warning
        />
      </Modal>
    </div>
  );
}

function TierChecklist(props){
  const getButtonDisplay = (att, tier) => {
    const isTierMatch = props.selectedTiers.includes(tier);
    if (att === "type") {
      return isTierMatch ? "primary" : "link";
    }
    else {
      return isTierMatch;
    }
  };

  return(
    <Row>
      <Col span={2} ></Col>
      <Col span={4} className="headerbutton-container">
        <Button onClick={() => props.onTierChange('0')} type={getButtonDisplay("type", '0')} ghost={getButtonDisplay("ghost", '0')} style={{ width: "100%" }}>0</Button>
      </Col>
      <Col span={4} className="headerbutton-container">
        <Button onClick={() => props.onTierChange('1')} type={getButtonDisplay("type", '1')} ghost={getButtonDisplay("ghost", '1')} style={{ width: "100%" }}>1</Button>
      </Col>
      <Col span={4} className="headerbutton-container">
        <Button onClick={() => props.onTierChange('2')} type={getButtonDisplay("type", '2')} ghost={getButtonDisplay("ghost", '2')} style={{ width: "100%" }}>2</Button>
      </Col>
      <Col span={4} className="headerbutton-container">
        <Button onClick={() => props.onTierChange('3')} type={getButtonDisplay("type", '3')} ghost={getButtonDisplay("ghost", '3')} style={{ width: "100%" }}>3</Button>
      </Col>
      <Col span={4} className="headerbutton-container">
        <Button onClick={() => props.onTierChange('N')} type={getButtonDisplay("type", 'N')} ghost={getButtonDisplay("ghost", 'N')} style={{ width: "100%" }}>N</Button>
      </Col>
    </Row>
  );
}