import React from 'react';
import { Rate } from 'antd';
import { Row, Col } from 'antd';

function MyInputStar(props) {
  const colSpan = props.colSpan ?? { label: 6, control: 18 };

  return (
    <>
      <Row gutter={[0, 8]}>
        <Col span={colSpan.label}>
          <label className='my-editor-label'>{props.label}</label>
        </Col>
        <Col span={colSpan.control}>
          <Rate
            count={3}
            value={props.value}
            onChange={(value) => { props.onChange(props.label, value); } }
          />
        </Col>
      </Row>
    </>
  );
}

export default MyInputStar;