import React, { useState, useEffect } from 'react';
import { Modal, Button, Input, Tree } from 'antd';
import withMyAlert from '../HOCs/withMyAlert';
import * as _uri from '../../_utils/UriHelper';
const axios = require('axios').default;

const { TreeNode, DirectoryTree } = Tree;

function DirNodeDialog(props) {
  const [nodes, setNodes] = useState([]);
  const [selected, setSelected] = useState(null);

  useEffect(() => {
    axios.get(_uri.GetLibraryDirNodes(1))
        .then((response) => {
          setNodes(response.data);
        })
        .catch((error) => {
          props.popApiError(error);
        });
  },[]);

  const treeHandlers = {
    loadData: (treeNode) =>{
      const result = new Promise(resolve => {
        if (treeNode.props.childs) {
          resolve();
          return;
        }
        axios.get(`${_uri.GetLibraryDirNodes(1)}?path=${treeNode.props.dataRef.name}`)
          .then((response) => {
            treeNode.props.dataRef.childs = response.data;
  
            setNodes((prev) => {
              return ([
                ...prev
              ]);
            });
            resolve();
          })
          .catch((error) => {
            props.popApiError(error);
          });
      });
  
      return result;
    },
    select: (key) => {
      setSelected(key);
    }
  }

  return (
    <Modal
      visible={props.visible}
      footer={null}
      centered={true}
      closable={false}
      onCancel={() => props.onClose()}
      style={{ width: 400, maxWidth: "95vh" }}
      //bodyStyle={{ backgroundColor: "grey", border: "1px solid black" }}
    >
      <MyDirTree 
        nodes={nodes} 
        onLoadData={treeHandlers.loadData}
        onSelect={treeHandlers.select}
      />
      <Button
        style={{ width: "100%" }} type="primary"
        onClick={() => { props.onOk(selected); props.onClose(); }}
      >
        Move Here
      </Button>
    </Modal>
  );
}

export default withMyAlert(DirNodeDialog);

function MyDirTree(props){
  const renderTreeNodes = (data) => {
    var result = data.map(item => {
      if (item.childs) {
        return (
          <TreeNode title={item.name} key={item.name} dataRef={item}>
            {renderTreeNodes(item.childs)}
          </TreeNode>
        );
      }
      return <TreeNode title={item.name} key={item.name} dataRef={item} />;
    });

    return result;
  };

  return (
    <DirectoryTree 
      loadData={props.onLoadData} 
      onSelect={(keys) => props.onSelect(keys[0])}
    >
      {renderTreeNodes(props.nodes)}
    </DirectoryTree>
  );
}