import React, { useState, useEffect, useCallback } from 'react';

import { Dropdown, Icon } from 'antd';
import { useInView } from 'react-intersection-observer';
import Flags from 'country-flag-icons/react/3x2';
import EditModalTrigger from '../../Albums/EditModalTrigger';

import withMyAlert from '../HOCs/withMyAlert';
import * as Helper from '../../_utils/Helper';
import * as _uri from '../../_utils/UriHelper';

function MyAlbumCard(props) {
  const handler = {
    view: function () {
      props.onView(props.albumCm)
    },

    edit: function () {
      if(props.onEdit) props.onEdit(props.albumCm.path);
    },
  }

  const [ref, inView, entry] = useInView({
    threshold: 0,
    rootMargin: "300px 0px 300px 0px"
  });

  const absoluteH = "150px";

  const menu = <EditModalTrigger onVisible={handler.edit}></EditModalTrigger>;
  return (
    <div ref={ref} style={{ paddingBottom: '8px', position: "relative", textAlign: "center" }}>
      <div style={{ height: absoluteH, width: "100%" }} >
        <Dropdown overlay={menu} trigger={['contextMenu']} disabled={!props.showContextMenu}>
        <div style={{
            display: "inline-block", position: "relative",
            height: absoluteH, width: "100%", maxWidth: absoluteH,
            borderLeft: !props.albumCm.isRead ? "1px solid #ddbe14" : "",
          }}
          onClick={handler.view}
        >
          {inView ? 
            <img
              style={{
                objectFit: "contain",
                height: "100%", width: "100%"
              }}
              src={_uri.StreamResizedImage(props.albumCm.coverInfo.uncPathEncoded, 150, props.type)}
              alt="img"
            /> : null
          }

          {props.albumCm.isWip ?
            <div style={{
              position: "absolute",
              height: 0,
              width: 0,
              left: "0px",
              top: "0px",
              lineHeight: 0
            }}>
              <Icon 
                type="pause-circle"
                style={{border:"1px solid rgba(108, 108, 108, 0.8)", borderRadius:"50%"}}
              />
            </div> : ""
          }

          {!props.albumCm.isRead ?
            <div style={{
              position: "absolute",
              height: 0,
              width: 0,
              left: "0px",
              top: "0px",
              borderLeft: "15px solid #ddbe14",
              borderRight: "15px solid transparent",
              borderBottom: "15px solid transparent"
            }} /> : ""
          }

          <div style={{
            position: "absolute",
            right: "3px",
            bottom: "3px",
            color: "white",
            lineHeight: "0px"
          }}>
            {props.albumCm.languages.map((item, i) => <FlagIcon language={item} key={i} />)}
          </div>
          <div style={{
            position: "absolute",
            right: "0px",
            bottom: "0px",
            height: "100%",
            width: "3px"
          }}>
            {[3, 2, 1].map((e, i) =>
              <div key={`tierBar-${i}`}
                style={{
                  width: "100%",
                  height: "33%",
                  backgroundColor: ColorFromIndex(props.albumCm.tier, e),
                  borderTop: BorderFromIndex(props.albumCm.tier, e),
                  borderRight: BorderRightFromIndex(props.albumCm.tier, e)
                }} />
            )}
          </div>
          <div style={{
            position: "absolute",
            left: "0px",
            bottom: "0px",
            width: "100%",
            height: "3px",
            backgroundColor: "white",
            display: props.albumCm.lastPageIndex > 0 ? "block" : "none"
          }}>
            <div style={{
              backgroundColor: "DodgerBlue",
              height: "100%",
              width: `${Helper.getPercent100(props.albumCm.lastPageIndex + 1, props.albumCm.pageCount)}%`
            }} />
          </div>
          {props.albumCm.note !== null && props.albumCm.note !== "" ?
            <div className="album-flag">
              {props.albumCm.note}
            </div> : ""
          }
          <div>
          </div>
        </div>
        </Dropdown>
      </div>
      <span style={{ fontSize: '12px', lineHeight: 1.1, color:"rgba(255,255,255, 0.8)" }}>
        {props.albumCm.fullTitle}
      </span>
    </div>
  );
}

function ColorFromIndex(tier, e) {
  const top = { r: 34, g: 193, b: 195 };
  const mid = { r: 142, g: 190, b: 121 };
  const bot = { r: 253, g: 187, b: 45 };

  const rgb = e === 3 ? top : e === 2 ? mid : bot;
  const alpha = tier >= e ? 1 : 0;

  return `rgba(${rgb.r},${rgb.g},${rgb.b},${alpha})`;
}

function BorderFromIndex(tier, e) {
  return tier > e && (e === 1 || e === 2) ? "2px solid black" : "0px";
}

function BorderRightFromIndex(tier, e){
  return tier >= e ? "1px solid black" : "0px";
}

function FlagIcon(props) {
  const flagStyle = { width: "18px", height: "12px", marginRight: "2px", marginBottom: "2px" };

  return (
    <>
      {
        props.language === "English" ? <Flags.GB style={flagStyle} /> :
          props.language === "Japanese" ? <Flags.JP style={flagStyle} /> :
            props.language === "Chinese" ? <Flags.CN style={flagStyle} /> :
              <Flags.ID style={flagStyle} />
      }
    </>
  );
}

export default withMyAlert(MyAlbumCard);