import React, { useState, useEffect, useRef } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Col } from 'antd';
import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';

import { InView } from 'react-intersection-observer';

import MyAlbumCard from '../_shared/Displays/MyAlbumCard';
import ReaderModal from '../_shared/ReaderModal/ReaderModal';
import EditModal from './EditModal';
import withMyAlert from '../_shared/HOCs/withMyAlert';
import PullToRefresh from 'react-simple-pull-to-refresh';
import * as _uri from '../_utils/UriHelper';
import { MobileOnly, IsMobile, IsPhone } from '../_utils/Display';

const { confirm } = Modal;
const axios = require('axios').default;

function Albums(props) {
  console.log("albums rendering");
  const type = 0;

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  //#region Display Album List
  const pageSize = 100;
  const [albumCms, setAlbumCms] = useState([]);
  const [maxPage, setMaxPage] = useState(0);
  const itemPerRow = IsPhone() ? 2 : IsMobile() ? 4 : 8;

  const handleRefresh = () => {
    return new Promise((resolve, reject) => {
      const { page, row, query } = props.queryParts;
      //const encodedQuery = (query !== undefined) ? encodeURIComponent(query) : "";
      axios.get(_uri.GetAlbumCardModels(type, page, row, query))
        .then((response) => {
          setAlbumCms(response.data);
          resolve("param from resolve");
        })
        .catch((error) => {
          props.popApiError(error);
          reject("param from reject");
        });
    });
  }
  useEffect(() => { handleRefresh(); }, [props.queryParts]);

  const [filteredAlbumCms, setFilteredAlbumCms] = useState([]);
  useEffect(() => {
    const acmsFiltered = albumCms.filter(a => { 
      return props.selectedTiers.includes(`${a.tier}`)
        || (props.selectedTiers.includes('N') && !a.isRead);
    });

    const acmsPaged = acmsFiltered.slice(0, maxPage * pageSize);

    setFilteredAlbumCms(acmsPaged);
  }, [albumCms, maxPage, props.selectedTiers]);

  function inViewChange(inView, entry){
    console.log('inViewChange', entry);
    if(entry.isIntersecting) setMaxPage(maxPage + 1);
  }
  //#endregion

  //#region Display Album Pages
  const [readerModalIsOpen, setReaderModalIsOpen] = useState(false);
  useEffect(() => {
    var body = document.body;
    if(readerModalIsOpen){
      body.style.overflow = "hidden";
    }
    else{
      body.style.overflow = "visible";
    }
  },[readerModalIsOpen])

  const [selectedAlbumCm, setSelectedAlbumCm] = useState(null);

  const readerHandler = {
    view: (albumCm) => {
      console.log('view', albumCm);

      setSelectedAlbumCm(albumCm);
      setReaderModalIsOpen(true);
    },

    close: (path, lastPageIndex) => {
      console.log('readerHandler close triggered');
      setReaderModalIsOpen(false);

      axios.post(_uri.UpdateAlbumOuterValue(type), {
        albumPath: path,
        lastPageIndex: lastPageIndex
      })
        .then((response) => {
        })
        .catch((error) => {
          props.popApiError(error);
        });

      setAlbumCms(albumCms.map((albumCm, aIndex) => {
        if (albumCm.path !== path) {
          return albumCm;
        }
        return {
          ...albumCm,
          lastPageIndex: lastPageIndex,
          isRead: lastPageIndex === albumCm.pageCount - 1 ? true : albumCm.isRead
        };
      }));
    }
  }
  //#endregion

  //#region Edit Delete
  const [editModalIsOpen, setEditModalIsOpen] = useState(false);
  const [editedAlbumPath, setEditedAlbumId] = useState(undefined);

  const crudHandler = {
    edit: (path) => {
      setEditModalIsOpen(true);
      setEditedAlbumId(path);
    },
    pageDeleteSuccess: (path) => {
      setAlbumCms(albumCms.map((albumCm, aIndex) => {
        if (albumCm.path !== path) {
          return albumCm;
        }
        return {
          ...albumCm,
          pageCount: albumCm.pageCount - 1
        };
      }));
    },
    chapterDeleteSuccess: (path, newPageCount) => {
      setAlbumCms(albumCms.map((albumCm, aIndex) => {
        if (albumCm.path !== path) {
          return albumCm;
        }
        return {
          ...albumCm,
          pageCount: newPageCount,
          lastPageIndex: 0
        };
      }));
    }
  }

  const editHandler = {
    ok: (editedAlbumVm) => {
      setEditModalIsOpen(false);
      axios.post(_uri.UpdateAlbum(type), editedAlbumVm)
        .then((response) => {
          console.log("edit success. Path: ", response.data);

          let newAlbumCms = [...albumCms];
          let editedAlbumVmIndex = newAlbumCms.findIndex(a => a.path === editedAlbumVm.path);
          newAlbumCms[editedAlbumVmIndex].fullTitle = `[${editedAlbumVm.album.artists.join(', ')}] ${editedAlbumVm.album.title}`;
          newAlbumCms[editedAlbumVmIndex].languages = editedAlbumVm.album.languages;

          newAlbumCms[editedAlbumVmIndex].note = editedAlbumVm.album.note;

          setAlbumCms(newAlbumCms);
        })
        .catch((error) => {
          //console.log
          props.popApiError(error);
        });
    },
    cancel: () => {
      setEditModalIsOpen(false);
    },
    delete: (albumVm) => {
      const album = albumVm.album;
      confirm({
        title: `Delete album [${album.artists.join(', ')}] ${album.title}?`,
        icon: <ExclamationCircleOutlined />,
        okText: 'YES',
        okType: 'danger',
        cancelText: 'NO',
        onOk() {
          axios.delete(_uri.DeleteAlbum(type, albumVm.path))
            .then(function (response) {
              setEditModalIsOpen(false);

              setAlbumCms(albumCms.filter(albumCm => {
                return albumCm.path !== albumVm.path;
              }));
            })
            .catch(function (error) {
              setEditModalIsOpen(false);
              props.popApiError(error);
            });
        },
        onCancel() {
        },
      });
    },
    refresh: (albumVm) => {
      setEditModalIsOpen(false);
      axios.get(_uri.RefreshAlbum(0, albumVm.path))
        .then((response) => {
          props.popInfo("Refresh Album", response.data, null);
        })
        .catch((error) => {
          props.popApiError(error);
        });
    },
    editFiles: (albumVm) => {
      setEditModalIsOpen(false);
      props.history.push("FileManagement?path=" + albumVm.path);
    }
  }

  //#endregion

  return (
    <>
      <PullToRefresh onRefresh={handleRefresh}>
        {/* <div style={{width:"100%", marginBottom:"5px"}}> */}
        <Row gutter={0} type="flex">
          {filteredAlbumCms.map((a, index) => {
            const rowIndex = Math.floor(index / itemPerRow);
            const bc = (index + rowIndex) % 2 === 0 ? '#333' : 'inherit';
            return (
              <Col style={{ textAlign: 'center', backgroundColor:bc }} lg={3} md={6} xs={12} key={"albumCol" + a.path}>
                <MyAlbumCard
                  index={index}
                  albumCm={a}
                  onView={readerHandler.view}
                  onEdit={crudHandler.edit}
                  //onDelete={crudHandler.delete}
                  showContextMenu={true}
                />
              </Col>
            );
          })}
        </Row>
        <InView as="div" onChange={inViewChange}>
          <div style={{width:"100%", height:"5px", backgroundColor:"transparent"}}></div>
        </InView>
      </PullToRefresh>
      <div style={{position:"fixed", top:0, backgroundColor:'rgba(0,0,0,0)'}}>
        <MobileOnly><div style={{position:"relative",height:"38px"}}></div></MobileOnly>
        <div style={{position:"relative", backgroundColor:'#2d2d2d'}}>
          {`Total: ${albumCms.length} Filtered: ${filteredAlbumCms.length}`}
        </div>
      </div>
      <ReaderModal
        isOpen={readerModalIsOpen}
        onClose={readerHandler.close}
        onPageDeleteSuccess={crudHandler.pageDeleteSuccess}
        onChapterDeleteSuccess={crudHandler.chapterDeleteSuccess}
        onOpenEditModal={()=>{
          setEditModalIsOpen(true);
          setEditedAlbumId(selectedAlbumCm?.path)
        }}
        albumCm={selectedAlbumCm}
      />
      <EditModal
        isOpen={editModalIsOpen}
        albumPath={editedAlbumPath}
        onOk={editHandler.ok}
        onCancel={editHandler.cancel}
        onDelete={editHandler.delete}
        onRefresh={editHandler.refresh}
        onEditFiles={editHandler.editFiles}
      />
    </>
  );
}

export default withRouter(withMyAlert(Albums));