import React, { useState, useEffect, useRef } from 'react';

import { Row, Col } from 'antd';
import useSWR from 'swr';

import MyAlbumCard from '../_shared/Displays/MyAlbumCard';
import * as _uri from "../_utils/UriHelper";

function Genres(props) {
  const uri = props.page === "Genres" ? _uri.GetGenreCardModels() :
    props.page === "Artists" ? _uri.GetFeaturedArtistCardModels() :
      null;

  const { data: albumCGroups, error } = useSWR(uri);

  if (error) { return <div>error!</div>; }
  if (!albumCGroups) { return <div>loading...</div>; }

  const readerHandler = {
    view: function (albumCm) {
      console.log("query click", albumCm);
      props.history.push('/albums?query=' + albumCm.path)
    },
  }

  return (
    <>
      {albumCGroups.map((acg, index) => (
        <Row key={"acgRow" + acg.name} gutter={0} type="flex" style={{ borderBottom: "1px solid rgb(83, 83, 83)", marginBottom: "5px" }}>
          { props.page === "This feature is cancelled" ? (
            <div style={{ position: "relative" }}>
              <div style={{ position: "absolute", top: "-18px", left: "10px" }}>{acg.name}</div>
            </div>) : ""
          }
          {acg.albumCms.map((a) => (
            <Col style={{ textAlign: 'center' }} lg={3} md={6} xs={12} key={"albumCol" + a.path}>
              <MyAlbumCard
                index={index}
                albumCm={a}
                onView={readerHandler.view}
                onEdit={() => { }}
                onDelete={() => { }}
                showContextMenu={false}
              />
            </Col>
          ))}
        </Row>
      ))}
    </>
  );
}

export default Genres;