import React, { useState, useEffect } from 'react';

import { Button, Divider, Progress, Input, Row, Col, Form, Select, Alert } from 'antd';

import { css } from '@emotion/css';
import ScrollToBottom from 'react-scroll-to-bottom';

import { IS_PUBLIC } from "../_utils/Config";
import withMyAlert from '../_shared/HOCs/withMyAlert';
import * as _uri from "../_utils/UriHelper";
import * as _helper from "../_utils/Helper";

const axios = require('axios').default;

const ROOT_CSS = css({
  height: 300,
  width: '100%'
});

function Setting(props){
  function setLoading(btnName, isLoading){
    setButton((prev) => {
      prev[btnName].isLoading = isLoading;
      prev[btnName].progress = isLoading ? 0 : 100;
      return {
        ...prev
      }
    });
  }

  function setDefaultButton(text,icon,name,uri){
    return {
      isLoading: false,
      percent: 0,
      eventMsgs: [],
      text: text,
      icon: icon,
      execute: () => {
        setLoading(name, true);

        const sse = new EventSource(uri, {});
        sse.onmessage = (res) => {
          const resData = JSON.parse(res.data); console.log('res data', resData);
          const isLoading = resData.currentStep !== resData.maxStep;
          if(!isLoading) sse.close();
          if(resData.isError) props.popError("API Error", resData.message);

          const percent = _helper.getPercent100(resData.currentStep, resData.maxStep);
          setButton(prev => { 
            prev[name].percent = percent;
            prev[name].isLoading = isLoading;
            prev[name].eventMsgs.push(resData);
            return {
              ...prev
            };
          });
        }
      }
    }
  }
  
  const [button, setButton] = useState({
    reload: setDefaultButton('Reload','reload','reload',_uri.ReloadDatabase()),
    fullScan: setDefaultButton('Full Scan', 'sync', 'fullScan', _uri.RescanDatabase()),
    //clean: setDefaultButton('Clean Library', 'delete','clean', _uri.CleanLibrary()),

    checkApiUrl:{
      isLoading: false,
      percent: 0,
      eventMsgs: [],
      text: 'Check Url',
      icon: 'check-circle',
      execute: null
    },
    checkMediaUrl:{
      isLoading: false,
      percent: 0,
      eventMsgs: [],
      text: 'Check Url',
      icon: 'check-circle',
      execute: null
    },
    checkResMediaUrl:{
      isLoading: false,
      percent: 0,
      eventMsgs: [],
      text: 'Check Url',
      icon: 'check-circle',
      execute: null
    },
  });

  return (
    <>
      <SettingForm {...props} />

      <Divider orientation="left" style={{color:"white"}}>
        Actions
      </Divider>
      <ActionButton {...button.reload} />
      <ActionButton {...button.fullScan} />
      {/* <ActionButton {...button.clean} /> */}

      <Divider orientation="left" style={{color:"white"}}>
        Event Stream
      </Divider>
    </>
  );
}

export default withMyAlert(Setting);

function SettingForm(props){

  const [apiBaseUrl, setApiBaseUrl] = useState(null);
  useEffect(() =>{
    setApiBaseUrl(_uri.GetApiBaseUrl());
  },[]);

  const [mediaBaseUrl, setMediaBaseUrl] = useState(null);
  useEffect(() =>{
    setMediaBaseUrl(_uri.GetMediaBaseUrl());
  },[]);

  const [resMediaBaseUrl, setResMediaBaseUrl] = useState(null);
  useEffect(() =>{
    setResMediaBaseUrl(_uri.GetResMediaBaseUrl());
  },[]);

  function checkUrl(url){
    //setLoading('checkApiUrl', true);
    axios.get(url)
      .then((response) => {
        //setLoading('checkApiUrl', false);
        props.popInfo(`${response.status} ${response.statusText}`, '', response.data);
      })
      .catch((error) => {
        //setLoading('checkApiUrl', false);
        props.popApiError(error);
      });
  }

  return(
    <>
      <RowBase textAlign='right'
        col0={
          <SettingFormLabel text='API'/>
        }
        col1={
          <Input.Search 
            value={apiBaseUrl} 
            onChange={(e) => { if(!IS_PUBLIC) setApiBaseUrl(e.target.value); }} 
            onSearch={() => { if(!IS_PUBLIC) checkUrl(apiBaseUrl); }}
            enterButton
          />
        }
      />
      <RowBase textAlign='right'
        col0={
          <SettingFormLabel text='Media'/>
        }
        col1={
          <Input.Search 
            value={mediaBaseUrl} 
            onChange={(e) => { if(!IS_PUBLIC) setMediaBaseUrl(e.target.value); }} 
            onSearch={() => { if(!IS_PUBLIC) checkUrl(mediaBaseUrl); }}
            enterButton
          />
        }
      />
      <RowBase textAlign='right'
        col0={
          <SettingFormLabel text='Res Media'/>
        }
        col1={
          <Input.Search 
            value={resMediaBaseUrl} 
            onChange={(e) => { if(!IS_PUBLIC) setResMediaBaseUrl(e.target.value); }} 
            onSearch={() => { if(!IS_PUBLIC) checkUrl(resMediaBaseUrl); }}
            enterButton
          />
        }
      />
      <RowBase textAlign='right'
        col1={ 
          <Button
            type="primary"
            onClick={() => {
              _uri.SaveApiBaseUrl(apiBaseUrl);
              _uri.SaveMediaBaseUrl(mediaBaseUrl);
              _uri.SaveResMediaBaseUrl(resMediaBaseUrl);
            }}
            style={{ width:"100%", maxWidth:"100px"}}
            disabled={IS_PUBLIC}
          >
            Save
          </Button>
        }
      />
    </>
  );

  function SettingFormLabel(props){
    const style={
      paddingTop:'4px',
      fontWeight:'bold'
    };

    return (
      <>
        <div style={{float:'left', ...style}}>{props.text}</div>
        <div style={{float:'right', ...style}}>:</div>
      </>
    );
  }
}

function ActionButton(props){
  const msgStr = props.eventMsgs.map(e => e.message).join('\n');
  return(
    <>
    <div style={{display:"flex", flexDirection:"row", marginBottom:"5px"}}>
      <div style={{width:"130px"}}>
      <Button
        icon={props.icon}
        type="primary"
        loading={props.isLoading}
        onClick={props.execute}
        block={true}
        style={{ width:"100%", maxWidth:"140px"}}
        disabled={IS_PUBLIC}
      >
        {props.text}
      </Button>
      </div>
      <div style={{
        height:"32px", 
        flex:"auto", 
        marginLeft:"10px",
        display:"flex",
        justifyContent: "flex-end",
        flexDirection: "column"
      }}>
        <Progress strokeLinecap="square" percent={props.percent} />
      </div>
    </div>
    <div hidden={props.eventMsgs.length === 0}
      style={{display:"flex", flexDirection:"row", marginBottom:"5px"}}>
      <div style={{width:"130px"}}></div>
      <div style={{
        //height:"32px", 
        flex:"auto",
        marginLeft:"10px",
        paddingRight:"32px"
      }}>
        <ScrollToBottom className={ROOT_CSS}>
          {props.eventMsgs.map(e => <p style={{marginBottom:"0.2em"}}>{e.message}</p>)}
        </ScrollToBottom>
      </div>
    </div>
    </>
  );
}

function RowBase(props){
  const style = {
    display:"flex", 
    flexDirection:"row", 
    marginBottom:"5px",
    textAlign: props.textAlign ?? 'left'
  };

  return(
    <>
    <div style={style}>
      <div style={{width:"130px"}}>
        {props.col0}
      </div>
      <div style={{
        height:"32px", 
        flex:"auto", 
        marginLeft:"10px",
        display:"flex",
        justifyContent: "flex-end",
        flexDirection: "column"
      }}>
        {props.col1}
      </div>
    </div>
    </>
  );
}