import React from 'react';
import { Modal } from 'antd';
import * as _uri from '../../_utils/UriHelper';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import moment from 'moment';

const { confirm } = Modal;
const axios = require('axios').default;

const withMyAlert = OriginalComponent => ({ ...props }) => {
  function deletePage(delObj, onSuccess, onError){
    confirm({
      title: `Delete ${delObj.alRelPath}?`,
      icon: <ExclamationCircleOutlined />,
      okText: 'YES',
      okType: 'danger',
      cancelText: 'NO',
      onOk() {
        axios.delete(_uri.DeleteFile(props.type, delObj.albumPath, delObj.alRelPath))
          .then(function (response) {
            onSuccess(response);
          })
          .catch(function (error) {
            onError(error);
          });
      },
      onCancel() {
      },
    });
  }

  function movePage(movObj, onSuccess, onError){
    axios.post(_uri.MoveFile(props.type), movObj)
      .then((response) =>{
        if (response.data.message === "Destination file already exist") {
          confirm({
            title: `Overwrite?`,
            icon: <ExclamationCircleOutlined />,
            content: <FileComparison file1={response.data.srcInfo} file2={response.data.dstInfo} />,
            okText: 'YES',
            okType: 'danger',
            cancelText: 'NO',
            onOk() {
              const newMovObj = {
                ...movObj,
                overwrite: true
              }

              movePage(newMovObj, onSuccess);
            },
            onCancel() {
            },
          });
        }
        else{
          onSuccess(response);
        }
      })
      .catch((error) => {
        onError(error);
      });
  }

  return (
    <OriginalComponent
      {...props}
      deletePage={deletePage}
      movePage={movePage}
    />
  )
}

export default withMyAlert;

function FileComparison(props) {
  const f1 = props.file1;
  const f2 = props.file2;

  return (
    <table class="comparison-table" style={{ width: "100%" }}>
      <tr>
        <th>Source</th>
        <th>Destination</th>
      </tr>
      <tr>
        <td>{f1.name}</td>
        <td>{f2.name}</td>
      </tr>
      <tr>
        <td style={getStyle(f1.size, f2.size)}>{(f1.size / 1024).toFixed(2)} KB</td>
        <td style={getStyle(f2.size, f1.size)}>{(f2.size / 1024).toFixed(2)} KB</td>
      </tr>
      <tr>
        <td style={getStyle(f1.createdDate, f2.createdDate)}>{moment(f1.createdDate).format('YYYY-MM-DD hh:mm')}</td>
        <td style={getStyle(f2.createdDate, f1.createdDate)}>{moment(f2.createdDate).format('YYYY-MM-DD hh:mm')}</td>
      </tr>
    </table>
  );

  function getStyle(a, b) {
    const fontWeight = a > b ? "bold" : "normal";
    return {
      fontWeight: fontWeight
    };
  }
}